import { GuiSelectedRow } from '../../../domain-api/gui.grid.public-api';
import { FormationCommandDispatcher } from '../../../../../structure/source/domain-api/formation/formation.command-dispatcher';
import { StructureId } from '../../../../../lib/structure/domain/structure.id';
import { SourceCommandDispatcher } from '../../../../../structure/source/domain-api/source.command-dispatcher';
import { SelectedRow } from '../../../../../structure/source/domain-api/formation/selected-row';
import { FormationWarehouse } from '../../../../../structure/source/domain-api/formation/formation.warehouse';

export interface GuiGridApi {

	getSelectedRows(): Array<GuiSelectedRow>;

	selectAll(): void;

	unselectAll(): void;

	deleteSelectedRows(): void;

	deleteRow(row: GuiSelectedRow): void;

	deleteRows(rows: Array<GuiSelectedRow>): void;

}

export class GuiGridApiProvider {

	private readonly structureId: StructureId;

	private readonly formationCommandDispatcher: FormationCommandDispatcher;

	private readonly formationWarehouse: FormationWarehouse;

	private readonly sourceCommandDispatcher: SourceCommandDispatcher;

	constructor(structureId: StructureId,
				formationCommandDispatcher: FormationCommandDispatcher,
				formationWarehouse: FormationWarehouse,
				sourceCommandDispatcher: SourceCommandDispatcher) {
		this.structureId = structureId;
		this.formationCommandDispatcher = formationCommandDispatcher;
		this.formationWarehouse = formationWarehouse;
		this.sourceCommandDispatcher = sourceCommandDispatcher;
	}

	provide(): GuiGridApi {

		const structureId = this.structureId,
			fcd = this.formationCommandDispatcher,
			fw = this.formationWarehouse,
			scd = this.sourceCommandDispatcher;

		return {

			deleteRow(row: GuiSelectedRow): void {
				scd.deleteRow(new SelectedRow(row.data, row.index, row.itemId), structureId);
			},

			deleteRows(rows: Array<GuiSelectedRow>): void {
				const selectedRows = rows.map((row: GuiSelectedRow) => new SelectedRow(row.data, row.index, row.itemId));

				scd.deleteRows(selectedRows, structureId);
			},

			deleteSelectedRows(): void {
				// this.sourceCommandDispatcher.
			},

			getSelectedRows(): Array<GuiSelectedRow> {

				const rows = fw.getSelectedRows(structureId);

				return rows.map((r: SelectedRow) => {
					return { data: r.getData(), index: r.getIndex(), itemId: r.getItemId() };
				});
			},

			selectAll(): void {
				fcd.selectAll(structureId);
			},

			unselectAll(): void {
				fcd.unselectAll(structureId);
			}

		};

	}


}
