import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { StructureId } from '../../../lib/structure/domain/structure.id';
import { SearchPhraseSetDomainEvent } from '../domain/set-phrase/search-phrase-set.domain-event';


@Injectable()
export class SearchEventRepository {

	constructor(private readonly domainEventBus: DomainEventBus) {
	}

	onSearchPhrase(structureId: StructureId): Observable<string> {

		return this.domainEventBus
				   .ofEvent(
					   SearchPhraseSetDomainEvent as any
				   )
				   .pipe(
					   filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString()),
					   filter((event: SearchPhraseSetDomainEvent) => event.isInitial() === false),
					   map((event: SearchPhraseSetDomainEvent) => event.getPhrase()),
					   filter((phrase: string) => phrase !== null)
				   );
	}
}
