import { ChangeDetectorRef, Component, ElementRef, Inject, Input, OnInit, PLATFORM_ID, ViewChild, ViewEncapsulation } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';

import { GuiGridGateway } from './gui.grid-gateway';
import { guiGridProviders } from './gui.grid.providers';

import { structureParentComponent } from '../../../../lib/structure/feature/structure-parent-component';

import { GuiGridIdGenerator } from './register/gui.grid-id.generator';
import { GuiGridRegister } from './register/gui.grid.register';
import { GridConsoleApiProvider } from './console/gui.grid.console.api';
import { GuiGridApi, GuiGridApiProvider } from './api/gui.grid.api-provider';
import { FormationCommandDispatcher } from '../../../../structure/source/domain-api/formation/formation.command-dispatcher';
import { SourceCommandDispatcher } from '../../../../structure/source/domain-api/source.command-dispatcher';
import { FormationWarehouse } from '../../../../structure/source/domain-api/formation/formation.warehouse';

@Component({
	selector: 'gui-grid',
	templateUrl: './gui.grid.component.html',
	styleUrls: [
		'../gui.grid.ngx.scss'
	],
	providers: [
		...guiGridProviders,
		{
			provide: structureParentComponent,
			useExisting: GuiGridComponent
		}
	],
	host: {
		'[class.gui-grid]': `"true"`,
		'[style.height]': 'maxHeight'
	},
	encapsulation: ViewEncapsulation.None
})
export class GuiGridComponent extends GuiGridGateway implements OnInit {

	@ViewChild('structure', { static: true })
	structureRef: any;

	@Input()
	gridId: string; // can be set once

	private localGridId: string;

	public api: GuiGridApi;

	constructor(@Inject(PLATFORM_ID) private readonly platformId: any,
				private readonly elementRef: ElementRef,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly gridRegister: GuiGridRegister,
				private readonly gridIdGenerator: GuiGridIdGenerator,
				// delegate
				private readonly formationCommandDispatcher: FormationCommandDispatcher,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly sourceCommandDispatcher: SourceCommandDispatcher,
				) {
		super();
	}

	ngOnInit() {
		this.initApi();

		let gridId = this.gridId;

		if (gridId === undefined) {
			gridId = this.gridIdGenerator.generateId();
		}

		this.localGridId = gridId;

		this.exposeGridId();

		// register
		this.gridRegister.register(gridId, this, this.structureRef.getStructureId());

		if (isPlatformBrowser(this.platformId)) {
			window['getGuiGrid'] = (gridId: string) => {

				const gridConsoleConfig = this.gridRegister.getValues(gridId);

				if (gridConsoleConfig) {
					return new GridConsoleApiProvider(gridConsoleConfig.component, gridConsoleConfig.structureId).provide();
				} else {
					return undefined;
				}
			};
		}
	}

	ngOnDestroy() {
		this.gridRegister.unregister(this.localGridId);
	}

	getElementRef(): ElementRef {
		return this.elementRef;
	}

	getGridId(): string {
		return '';
	}

	detectChanges(): void {
		this.changeDetectorRef.detectChanges();
	}

	private exposeGridId(): void {
		this.elementRef.nativeElement.setAttribute('gui-grid-id', this.localGridId);
	}

	private initApi(): void {
		this.api = new GuiGridApiProvider(
			this.structureRef.structureId,
			this.formationCommandDispatcher,
			this.formationWarehouse,
			this.sourceCommandDispatcher
		).provide();
	}

}
