import { Injectable } from '@angular/core';

import { StructureId } from '../../../lib/structure/domain/structure.id';
import { structureGlobalId } from '../../../lib/structure/domain-api/structure.global-id';
import { SortingConfig } from './sorting-config';
import { SetSortingCommand } from '../domain/set-config/set-sorting.command';
import { FieldId } from '../../field/domain/core/field/field.id';
import { CompositionId } from '../../../lib/composition/domain/composition.id';
import { ToggleSortCommand } from '../domain/toggle/toggle-sort.command';
import { SortOrder } from '../../../lib/composition/domain/column/sort/sort-order';
import { SetSortOrderCommand } from '../domain/order/set-sort-order.command';
import { CommandDispatcher } from '@generic-ui/hermes';


@Injectable()
export class SortingCommandDispatcher {

	constructor(private readonly commandDispatcher: CommandDispatcher) {
	}

	setSortingConfig(config: SortingConfig, structureId: StructureId = structureGlobalId): void {
		this.commandDispatcher.dispatch(new SetSortingCommand(structureId, config));
	}

	toggleSort(fieldId: FieldId, compositionId?: CompositionId, structureId: StructureId = structureGlobalId): void {
		this.commandDispatcher.dispatch(new ToggleSortCommand(structureId, compositionId, fieldId));
	}

	setSortOrder(fieldId: FieldId, sortOrder: SortOrder, compositionId?: CompositionId, structureId: StructureId = structureGlobalId): void {
		this.commandDispatcher.dispatch(new SetSortOrderCommand(structureId, compositionId, fieldId, sortOrder));
	}

}
