import { GuiGridComponent } from '../gui.grid.component';

export interface GuiGridConsoleApi {

	changeColumnHeaderTop(enabled: boolean): void;


}

export class GridConsoleApiProvider {

	constructor(private readonly gridComponent: GuiGridComponent,
				private readonly structureId: any) {
	} // StructureId

	provide(): GuiGridConsoleApi {

		const component = this.gridComponent;

		return {
			changeColumnHeaderTop(enabled: boolean): void {
				component.columnHeaderTop = enabled;
				component.detectChanges();
			}
		};

	}

}
